import React, { useState } from 'react'

function Calculator({id,
    tileArea,
    perPack,
price}) {
const [calc, setCalc] = useState (false);
const [length, setLength] = useState (0);
const [width, setWidth] = useState (0);
const [cost, setCost] = useState (0);
const [reqBoxes, setReqBoxes] = useState (0);

    const handleSubmit = (e) => {
        e.preventDefault();
       calculate()
        setCalc(true)

    }

    function calculate (){
const reqArea = (length * width);
const numTiles = Math.ceil(reqArea/tileArea) ;
const reqBoxes = Math.ceil(numTiles/perPack);
const cost = (reqBoxes*price);
setCost(cost);
setReqBoxes(reqBoxes);
return cost;
    };

    return (
        <div>
            <h3>Calculator</h3>
            <form onSubmit={handleSubmit}>
            <label for="length">Length cm </label>
  <input 
  type="number" 
  name="length" 
  id="length" 
  value={length}
  onChange={(e) => setLength(e.target.value)}
  required/>
  <label for="width"> Width cm </label>
  <input 
  type="number" 
  name="width" 
  id="width" 
  value={width}
  onChange={(e) => setWidth(e.target.value)}
  required/>
  <button className="btn" type="submit">Submit</button>
            </form>
    {calc && <p> total cost ${cost} and number of boxes required is {reqBoxes}</p>}
        </div>
    )
}

export default Calculator
